import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class LikeService {
  likes = [];
  constructor() { }
  addLikes(product) {
    if (!this.likes.some(v => v.id === product.id)) {
      this.likes.push(product);
      alert('添加到我的!');
    }
  }

  removeLike(product) {
    this.likes.some((v, i, arr) => {
      if (v.id === product.id) {
        arr.splice(i, 1);
        alert('取消!');
      }
    });
  }

  getLikes() {
    return this.likes;
  }

  clearLikes() {
    this.likes = [];
    return this.likes;
  }
}
